            animation: fadeout 1s ease-in-out forwards;
}

@-webkit-keyframes fadeout {
    from {
        -webkit-transform: none;
                transform: none;
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes fadeout {
    from {
        -webkit-transform: none;
                transform: none;
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
